.friendMessage {
  text-align: left;
}

.friendImageAndText {
  position: relative;
  display: flex;
  width: 50%;
}

.friendImageAndText img {
  width: 48px;
  height: 48px;
}

.friendText {
  position: relative;
  right: -25px;
  background-color: #f5f7fb;
  border-radius: 20px 20px 20px 20px;
  padding: 15px;
}

.friendText:after {
  content: "";
  position: absolute;
  bottom: 0px;
  right: 300px;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-bottom: 20px solid #f5f7fb;
  border-right: 20px solid #f5f7fb;
}

.friendName {
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 600;

  text-align: left;
}

.friendMessageText {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
}

.friendTime {
  font-size: 14px;
  font-family: Montserrat;
  color: #000000;
}
